/* global conditionalblocks */
import apiFetch from '@wordpress/api-fetch';

const { useState } = wp.element;

function stateManager() {
	const [cbState, setcbState] = useState(conditionalblocks);

	// Enable Dev mode on .local domains automatically.
	const [isDevMode, setIsDevMode] = useState(window.location.href.indexOf('.local') !== -1);

	const [isSaving, setIsSaving] = useState(false);

	const toggleDevMode = () => {
		setIsDevMode(!isDevMode);
	};

	const get = (optionKey, additionalKey) => {
		if (optionKey && additionalKey) {
			return cbState?.[optionKey]?.[additionalKey] ? cbState[optionKey][additionalKey] : false;
		}

		if (optionKey) {
			return cbState?.[optionKey] ? cbState[optionKey] : false;
		}

		return cbState;
	};

	/**
	 * Helper function to deep merge two objects.
	 * @param {object} target
	 * @param {object} source
	 */
	function deepObjectMerge(target, source) {
		const result = { ...target, ...source };
		const keys = Object.keys(result);

		keys.forEach((key) => {
			const targetProp = target[key];
			const sourceProp = source[key];
			// if two objects are in conflict
			if (typeof targetProp === 'object' && typeof sourceProp === 'object') {
				result[key] = deepObjectMerge(targetProp, sourceProp);
			}
		});

		return result;
	}

	/**
	 * Pass our state to the REST API which will handle the rest.
	 *
	 * @param {*} modifed
	 */
	const update = async (modifed, deepMerge = true) => {
		setIsSaving(true);
		const updatedState = deepMerge ? deepObjectMerge({ ...cbState }, modifed) : { ...cbState, ...modifed };

		setcbState(updatedState);

		await apiFetch({ path: 'conditional-blocks/v1/update', method: 'POST', data: { ...updatedState } }).then((res) => {
			if (isDevMode) {
				console.log(res);
			}
		});

		setIsSaving(false);
	};

	/**
	 * Pass our state to the REST API which will handle the rest.
	 *
	 * @param {*} modifed
	 */
	const convertLegacyConditions = async (legacyConditions) => {
		const converted = await apiFetch({ path: 'conditional-blocks/v1/convert-legacy-conditions', method: 'POST', data: { legacyConditions } }).then((res) => res);

		return converted;
	};

	return { cbState, get, update, convertLegacyConditions, isDevMode, toggleDevMode, isSaving };
}

export default stateManager;
