import Select from 'react-select';
/**
 * WordPress dependencies
 */
const { useState } = wp.element;
const { useSelect } = wp.data;

const TermSelect = (props) => {
	const { postType, taxonomy } = props;
	const [searchKeyword, setSearchKeyword] = useState(false);

	const params = {
		per_page: 20,
		search: searchKeyword
	};

	const fetchRecords = useSelect(
		(select) => ({
			data: select('core').getEntityRecords('taxonomy', taxonomy, params),
			isRequesting: select('core/data').isResolving('core', 'getEntityRecords', postType)
		}),
		[taxonomy, searchKeyword]
	);

	return (
		<div>
			<Select
				classNamePrefix='conditional-blocks-select'
				className='conditional-blocks-select-container'
				isSearchable
				isLoading={!fetchRecords?.data}
				options={fetchRecords?.data?.map((term) => ({ label: `${term.name} (ID: ${term.id})`, value: term.id }))}
				onInputChange={(val) => setSearchKeyword(val)}
				{...props}
			/>
		</div>
	);
};

export default TermSelect;
