import Select from 'react-select';
/**
 * WordPress dependencies
 */
const { useState } = wp.element;
const { useSelect } = wp.data;

const PostTypeSelect = (props) => {
	const [searchKeyword, setSearchKeyword] = useState(false);

	const fetchRecords = useSelect(
		(select) => {
			const params = {
				per_page: 30,
				search: searchKeyword
			};

			const excludedPostTypes = ['attachment'];

			return {
				postTypes: select('core')
					.getPostTypes(params)
					?.filter(({ viewable, slug }) => viewable && !excludedPostTypes.includes(slug)),
				isRequesting: select('core/data').isResolving('core', 'getEntityRecords', [params])
			};
		},
		[searchKeyword]
	);

	return (
		<div>
			<Select
				classNamePrefix='conditional-blocks-select'
				className='conditional-blocks-select-container'
				isSearchable
				isLoading={fetchRecords.isRequesting}
				options={fetchRecords?.postTypes?.map((postType) => ({ label: `${postType.name} (${postType.slug})`, value: postType.slug }))}
				onInputChange={(val) => setSearchKeyword(val)}
				{...props}
			/>
		</div>
	);
};

export default PostTypeSelect;
